import React, { useState, useEffect, Component } from 'react';
import {Link} from 'react-router-dom'
import "./All.css"

function useFetch(url) {

    let [data, setData] = useState([]);

    useEffect(() => {
        fetch(url)
            .then(res => res.json())
            .then(res => {
                console.log(res)
                setData(res.data)
            })
    })
    return data;
}
function Topic() {
    let data = useFetch('https://cnodejs.org/api/v1/topics/?tab=good');
    // console.log(data);
    return (
        <div>
            {
                data.map((item) => <li key={item.id}><img src={item.author.avatar_url}/>
                <span id="title1">{item.reply_count}/{item.visit_count}</span><Link to='content'><button id='title'>{item.title}</button></Link>
                <span id="small_1">2小时前</span><img src={item.author.avatar_url} id="small"/></li>)
            }
        </div>

    )

}
export default class Good extends Component {
    render() {
        return (
            <div>
                <Topic/>
            </div>
        )
    }
}
